<template>
  <div class="secretAuthentication">
    <div class="prompt">
      <h3>ANSWER THE QUESTION TO PROVE YOU'RE ME</h3>
      <h1>{{question}}</h1>
      <input type="text" v-on:keyup=submitAnswer placeholder="choose carefully...">
      <div v-if=answered id="response" v-html=response></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'SecretAuthentication',
  components: {
  },
  data(){
    const questionAndAnswers = [
    {question:"What is my favorite color?",answer:"rainbow"},
    {question:"What is my all-time favorite TV show?",answer:"avatar"},
    {question:"What's my favorite saucy food?",answer:"ribs"},
    ];
    const chosenPair = questionAndAnswers[Math.floor(Math.random()*questionAndAnswers.length)];
    const question = chosenPair.question;
    const correctAnswer = chosenPair.answer;
    const response = "Wrong choice, you poopyhead!!"
    return{
      question:question,
      correctAnswer:correctAnswer,
      response:response,
      answered:false
    }
  },
  mounted(){
  },
  methods:{
    submitAnswer(e){
      if (e.keyCode === 13 && e.target.value !== null) {
        this.answered = true;
        let submittedAnswer = e.target.value;
        if (submittedAnswer == this.correctAnswer){
          let thisThing = this;

          this.response = "You know me so well!"
          setTimeout(function(){
            thisThing.$emit('authenticationSuccess');
          },2000)
         
        } else {
          let thisThing = this;
          setTimeout(function(){ 
            thisThing.$emit('authenticationFail');
          },2000)
          
        }
      }
    }
  },
  props: {

  }
}
</script>

