<template>
  <div class="flute">
    <Navigation currentAppName="Flute tunes" appId="flute"/>
    <h1>Flute</h1>

    <button v-show="isPlaying" v-on:click="stopMusic()">Stop</button>
    <button v-show="!isPlaying" v-on:click="playMusic()">Play</button>
  </div>
</template>

<script>
import Navigation from './Navigation.vue';

export default {
  name: 'Flute',
  components: {
    Navigation

  },
  data(){
    return{
      // poop:null
      isPlaying: true
    }
  },
  mounted(){

  },

  methods:{
    stopMusic(){
      console.log('emitting stop music');
      this.$root.$emit('stopMusicMessage');
      this.isPlaying = false;
    },
    playMusic(){
      console.log('emitting play music');
      this.$root.$emit('playMusicMessage');
      this.isPlaying = true;
    }

  }
}
</script>



