<script>
import $ from 'jquery';
  
export default {
  name: 'Bedroom',
  props: {
    msg: String,
    unreadMessage: Boolean
  },
  mounted(){
    let star = $('.star')[0];
    let cloud = $(".cloud")[0];
    for (var i = 0; i < 200; i++){
      let starCopy = $(star).clone();
      let starCSS = {
        left: Math.random()*window.innerWidth, 
        top: Math.random()*window.innerHeight, 
        size: Math.random()*5
      };
      $(starCopy).css({
        'left': `${starCSS.left}px`,
        'top': `${starCSS.top}px`,
        'width': `${starCSS.size}px`,
        'height': `${starCSS.size}px`,
        });
      $(starCopy).appendTo("#starContainer");  
    }

    for (var j = 0; j < 20; j++){
      let cloudCopy = $(cloud).clone();
      let cloudCSS = {
        left: Math.random()*1600, 
        top: Math.random()*window.innerHeight, 
        width: 100+Math.random()*100, 
        opacity: Math.random()*.5,
        duration:40+Math.random()*60
      };
     $(cloudCopy).css({
        'left':`${cloudCSS.left}px`,
        'top': `${cloudCSS.top}px`,
        'width': `${cloudCSS.width}px`,
        'opacity': `${cloudCSS.opacity}`,
        'animation-duration':`${cloudCSS.duration}s`
        });
      $(cloudCopy).appendTo("#starContainer");
    }
    $('.bedroom').delay(2000).animate({opacity:1},2000);
  },
  methods:{
    // openApp(e){
    //   // console.log(e.target);
    //   var appId = e.target.getAttribute('value');
    //   this.$emit('messageFromBedroom',appId);
    // }
    openApp(appId){
      this.$emit('messageFromBedroom',appId);
    }
  }
}
</script>

