<template>
  <div class="selector" >
    <label v-bind:for="selectorType"> choose a {{selectorType}}</label>
    <select  v-bind:id=selectorName >
      <option v-for="option in options" v-bind:value=option :key=option v-bind:selected="option == selected">{{option}}</option>
      
    </select>
  </div>
</template>

<script>
// import Navigation from './Navigation.vue';
// import Postcard from './Postcard.vue';
// import * as database from '../database.js';

// import  $ from 'jquery';


export default {
  name: 'Selector',
  components: {

  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"

  },
  data(){
    let options = [];
    let selected;
    switch(this.selectorType){
      case "day":
        for (var i=1; i < 32; i++){
          options.push(i);
        }
        selected = 13;
        break;
      case "month":
        for (var j=1; j < 13; j++){
          options.push(j);
        }
        selected = 5;
        break;
      case "year":
        for (var k=1996; k < 2090; k++){
          options.push(k);
        }
        // console.log(this.direction);
        if (this.direction == "future") {
          selected = 2007;
        } else { selected = 2005; }
        break;
    }

    // console.log(options);
    return {
      options:options,
      selected:selected
    }
    
  },
  computed:{
    selectorName(){
      // console.log("selector-" + this.direction+"-"+this.selectorType);
      return "selector-" + this.direction+"-"+this.selectorType;
    }
  },
  mounted(){
    let selector = document.getElementById("selector-"+this.direction+"-"+this.selectorType);

    // if (this.direction == "future"){
    //   selector = this.$refs.selectorfuture;
    // } else {
    //   selector = this.$refs.selectorpast;
    // }
    let value = selector.options[selector.selectedIndex].value;
    let data = {
      selectorType: this.selectorType,
      value: value
    }
    this.$emit("updateDateMessage",data);

    let _this = this;
    selector.addEventListener("change",function(){
       let selector = document.getElementById("selector-" + _this.direction+"-"+_this.selectorType);
      // let selector;
      // console.log(_this.$refs);
      // if (this.direction == "future"){
      //   selector = _this.$refs.selectorfuture;
      // } else {
      //   selector = _this.$refs.selectorpast;
      // // }
      // console.log(selector);
      // console.log("selector has changed");
      let value = selector.options[selector.selectedIndex].value;
      let data = {
        selectorType: _this.selectorType,
        value: value
      }
      _this.$emit("updateDateMessage",data);
    });

  },
  methods:{

  },
  props: {
    selectorType: String,
    direction: String

  }
}
</script>

