<template>
  <div class="messages" >
    <Navigation currentAppName="Postcards" appId="messages" />

    <!-- <h1>Postcards</h1> -->
    <button class="bold createNewPostcard" v-on:click="newPostcard">New Postcard</button>
<!--     {{incomingPostcards}} -->
    <!-- <Postcard /> -->
    <div v-show="!creating">
            <div class="postcard-space">

        <div class="postcard-element unselected" 
        v-bind:style="{'left':randomPositions[postcard.id % 30].leftCSS,'top':randomPositions[postcard.id % 30].topCSS}"

        v-on:mousedown="dragPostcard"
        v-on:mousemove="continueDragging"
        v-on:mouseup="stopDragging"
        :ref="postcard"
        v-for="postcard in allPostcards" :key=postcard.id
         v-bind:class='{"inView":isPostcardInView(postcard),
         "leaveToRight":randomPositions[postcard.id % 30].leaveToRight,
          "unreadPostcard":postcard.unread}'>

          <Postcard  
          v-on:madePostcardUnreadMessage="setPostcardToRead"
          v-on:readPostcardMessage="readPostcard"
          v-bind:postcardData=postcard
          v-bind:reset="resetReadPostcards"
          v-bind:message=postcard.message
          v-bind:imgSrc=postcard.imgSrc
          v-bind:canvasData=postcard.canvasData
          v-bind:fromData=postcard.fromData
          v-bind:toData=postcard.toData
          v-bind:unread=postcard.unread />
        </div>
      </div>
      
      <div class="messages-nav">
         <a  v-show=readingPostcard.currentlyReading class='backButton' id="stopReadingButton" v-on:click=stopReading()>&#8592; Back</a>

          <h3 v-bind:class="{active:(this.messagesView=='incoming')}"  v-on:click="changeMessagesView('incoming')">Incoming</h3>
          <h3 v-bind:class="{active:(this.messagesView=='outgoing')}" v-on:click="changeMessagesView('outgoing')">Outgoing</h3>
          <h3 v-bind:class="{active:(this.messagesView=='myself')}" v-on:click="changeMessagesView('myself')">With myself</h3>
      </div>
    </div>
    <div v-show="creating">
      <a class='backButton' v-on:click=goBack()>&#8592; Back</a>
      <div ref="creating1" class="creating-step">
        <h3>Write a postcard for...</h3>
        <div v-on:click="handleRecipient('myself')" class="creating-selection">Myself</div>
         <div  v-on:click="handleRecipient('else')" class="creating-selection">Someone else</div>
      </div>
      <div ref="creating2TimeChoice" class="creating-step">
        <h3>Write a postcard for myself...</h3>
        <div v-on:click="handleTime('past')" class="creating-selection">In the past</div>
         <div  v-on:click="handleTime('future')" class="creating-selection">In the future</div>
      </div>
      <div  ref="creating3SelectFuture" class="creating-step">
        <h3>Select a date in the future</h3>
        <Selector v-on:updateDateMessage="updateDateFuture" selectorType="month" direction="future" />
        <Selector v-on:updateDateMessage="updateDateFuture" selectorType="day" direction="future" />
        <Selector v-on:updateDateMessage="updateDateFuture" selectorType="year" direction="future" />
        <button v-on:click="handleDate('future')">Next</button>
      </div>
      <div ref="creating3SelectPast" class="creating-step">
        <h3>Select a date in the past</h3>
        <Selector v-on:updateDateMessage="updateDatePast" selectorType="month" direction="past" />
        <Selector v-on:updateDateMessage="updateDatePast" selectorType="day" direction="past" />
        <Selector v-on:updateDateMessage="updateDatePast" selectorType="year" direction="past" />
        <button v-on:click="handleDate('past')">Next</button>
      </div>

      <div ref="creating2ChooseEmail" class="creating-step">
        <h3>Choose someone to send this to</h3>
        <input v-on:keyup="typeEmail" ref='emailInput' type="text" placeholder="Type an email address">
        <button v-bind:class="{'disabled': !emailValid}" v-on:click="handleEmail">Next</button>
      </div>
      <div ref="postcardCreation" class="creating-step">
        <h1 v-if="newPostcardToData.name == 'jackie@jackies.world'">Send a postcard to yourself in {{newPostcardToData.date}}</h1>
        <h1 v-else>Send a postcard to {{newPostcardToData.name}}</h1>
        <NewPostcard
        v-for="design in postcardDesigns" :key=design.name
         v-on:selectPostcardMessage="handleSelectedPostcard"
        v-on:postcardContentMessage="handleMessageTyping"
        v-bind:message=messageContentToSend
        v-bind:design=design
        canvasData=""
        v-bind:fromData=newPostcardFromData
        v-bind:toData=newPostcardToData
        :unread=false
        v-bind:reset=resetPostcards />

        <button v-bind:class="{'disabled':(selectedPostcard == null || messageContentToSend.length == 0)}" class="bold sendPostcard" v-on:click="sendPostcard">send postcard</button>
        <div v-show="sendSuccess || sendFailure" id="sendPostcardResponse">
          <div class="content">
            <div v-if="sendSuccess">
              <h1 v-if="newPostcardToData.name == 'jackie@jackies.world'">Your postcard will be sent to yourself on {{newPostcardToData.date}}!</h1>
              <h1 v-else>Your postcard has been sent to {{newPostcardToData.name}}!</h1>
              <button class="bold" v-on:click="backFromSendSuccess">Go back to postcards</button>
            </div>
            <div v-if="sendFailure">
              <h1>Something went wrong :/ Check your address and try again!</h1>
              <button  class="bold" v-on:click="backFromSendFailure">Go back</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navigation from './Navigation.vue';
import Postcard from './Postcard.vue';
import NewPostcard from './NewPostcard.vue';
import Selector from './Selector.vue';
import * as database from '../database.js';
import  $ from 'jquery';

export default {
  name: 'Messages',
  components: {
    Navigation,
    Postcard,
    Selector,
    NewPostcard

  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"

  },
  data(){
    let allPostcards = database.getPostcards();
    let createStepDisplayed = this.$refs.creating1;
    let randomPositions = new Array(30);
    for (var i=0; i < randomPositions.length; i++) {
      let randomPlacement = this.randomPostcardPlacement();
      randomPositions[i] = randomPlacement;
    }

    let postcardDesigns = [
      {name:"Flavor Packet",src:"./postcards/flavor-packet.png"},
      {name:"Lunar New Year",src:"./postcards/red-envelope.png"},
      {name:"On Vacation",src:"./postcards/greetings.png"}
    ];

    return {
      //Helpers for postcard creation process
      creating:false,
      createStepDisplayed: createStepDisplayed,
      selectedDate: {},
      createStepBeforePostcard: null,
      selectedPostcard: null,
      resetPostcards:false,
      postcardDesigns: postcardDesigns,
      sendSuccess: false,
      sendFailure: false,
      //Data used for writing new postcard 
      newPostcardToData: {name: "",date:""},
      newPostcardFromData: {name:"jackie@jackies.world",date:"05132006"},
      emailValid:false,
      messageContentToSend: "",
      selectedThemeSrc:"",
      //Helpers for viewing postcards
      allPostcards: allPostcards,
      messagesView: "incoming",
      draggingPostcard: {isDragging:false,el:null,grabbedCoords:{x:0,y:0}},
      readingPostcard: {currentlyReading: false, el: null},
      resetReadPostcards:false,
      randomPositions: randomPositions,
      animationFinished: true,
      postcardsThatHaveBeenRead: []

    }
    
  },
  computed:{

  },
  mounted(){
    this.changeMessagesView('incoming');
    window.addEventListener("mousemove",this.continueDragging);
    $('.postcard-space').delay(300).animate({opacity:1},1300)
    let _this = this;
    $('.goToBedroomLink').click(()=>{
      if (_this.postcardsThatHaveBeenRead.length > 0){
        this.$root.$emit('removePostcardNotificationMessage');
      }
      _this.postcardsThatHaveBeenRead.forEach(function(postcardData){
        database.updateData_Postcard(postcardData);
      })
    })
  },
  methods:{
      randomPostcardPlacement: function(){
        let left = 100+this.random0to1()*(window.innerWidth-450);
        let top = 100+this.random0to1()*(window.innerHeight-600);
        let leaveToRight = (left > window.innerWidth/2);
        return {leftCSS:`${left}px`,topCSS:`${top}px`,leaveToRight:leaveToRight}
      },
      changeMessagesView: function(type){
        let _this = this;
        let randomizePositions = function(){
          $(".postcard-element").each(function(){
            let randomPlacement = _this.randomPostcardPlacement();
              $(this).css('left', randomPlacement.leftCSS);
              $(this).css('top', randomPlacement.topCSS);
              $(this).removeClass('leaveToRight');
              if (randomPlacement.leaveToRight){
                $(this).addClass('leaveToRight');
             }
          });
          $(".postcard-element").css('transition','transform 1s, top 1s, left 1s');
          setTimeout(function(){
            $(".postcard-element").css('transition','transform 3s');
          },1000)
        }

        if (this.readingPostcard.currentlyReading){
          //Reset all the postcard things here
          this.stopReading();
        }
        switch(type){
          case "incoming":
            if (this.messagesView != "incoming"){
              this.messagesView = "incoming";
              randomizePositions();
            }
            break;
          case "outgoing":
            if (this.messagesView != "outgoing"){
              this.messagesView = "outgoing";
              randomizePositions();
            }
            break;
          case "myself":
            if (this.messagesView != "myself"){
              this.messagesView = "myself";
              randomizePositions();
            }
            break;
        }
      },

      isPostcardInView(postcard){
        let isInView = false;
        switch(this.messagesView){
        case "incoming":
          isInView =  (postcard.toData.name =='jackie@jackies.world')
          break;
        case "outgoing":
          isInView = (postcard.fromData.name == 'jackie@jackies.world')
          break;
        case "myself":
          isInView = (postcard.fromData.name == 'jackie@jackies.world' && postcard.toData.name == "jackie@jackies.world")
          break;
        } 
        return isInView;  
     },
    random(){
      let random = Math.random();
      return (random > .5);
    },
    random0to1(){
      return Math.random();
    },
    newPostcard(){
      // console.log("creating a newpostcard");
      this.creating = true;
      this.creatingStepDisplayed = this.$refs.creating1;
      $(".creating-step").hide();
      this.transitionStep(this.$refs.creating1);
    },
    goBack(){
      var currentStep = this.creatingStepDisplayed;
      let nextStep;
      switch(currentStep){
        case this.$refs.creating1:
          //Go back to main
          this.creating = false;
          this.creatingStepDisplayed = this.$refs.creating1;
          break;
        case this.$refs.creating2ChooseEmail:
          this.creatingStepDisplayed = this.$refs.creating1;
          nextStep = this.$refs.creating1;
          break;
        case this.$refs.creating2TimeChoice:
          this.creatingStepDisplayed = this.$refs.creating1;
          nextStep = this.$refs.creating1;
          break;
        case this.$refs.creating3SelectPast:
          this.creatingStepDisplayed = this.$refs.creating2TimeChoice;
          nextStep = this.$refs.creating2TimeChoice;
          break;
        case this.$refs.creating3SelectFuture:
          this.creatingStepDisplayed = this.$refs.creating2TimeChoice;
          nextStep = this.$refs.creating2TimeChoice;
          break;
        case this.$refs.postcardCreation:
          if (this.selectedPostcard == null){
             this.creatingStepDisplayed = this.createStepBeforePostcard;
            // console.log(this.createStepBeforePostcard);
            nextStep = this.createStepBeforePostcard;
          } else {
            //Otherwise just allow postcards to change theme
            $(".newPostcard").removeClass("hidden");
            this.resetPostcards = true;
            this.selectedPostcard = null;
          }
          break;
      }
      if (nextStep != null){
        this.transitionStep(nextStep);
      }
    },
    updateDatePast(data){
      if (this.creatingStepDisplayed == this.$refs.creating2SelectPast){
        let type = data.selectorType;
        let value = data.value;
        this.selectedDate[type] = value;    
      }

    },
    updateDateFuture(data){
      if (this.creatingStepDisplayed == this.$refs.creating2SelectFuture){
        let type = data.selectorType;
        let value = data.value;
        this.selectedDate[type] = value;
      }
    },
    //Step 1
    handleRecipient(recipient){
      switch(recipient){
        case "myself":
          this.creatingStepDisplayed = this.$refs.creating2TimeChoice;
          this.transitionStep(this.$refs.creating2TimeChoice);
          break;
        case "else":
          this.creatingStepDisplayed =this.$refs.creating2ChooseEmail;
          this.transitionStep(this.$refs.creating2ChooseEmail);
          break;
      }
    },
    handleEmail(){
      this.newPostcardToData.name = this.$refs.emailInput.value;
      this.newPostcardToData.date = '05132006';
      this.creatingStepDisplayed = this.$refs.postcardCreation;
      this.transitionStep(this.$refs.postcardCreation);
      this.createStepBeforePostcard = this.$refs.creating2ChooseEmail;
    },
    typeEmail(){
      let value = this.$refs.emailInput.value;
      this.emailValid = (value != "");
    },
    handleTime(direction){
      switch(direction){
        case "past":
          this.creatingStepDisplayed = this.$refs.creating3SelectPast;
          this.transitionStep(this.$refs.creating3SelectPast);
          this.selectedDate = {'month':5,'day':13,'year':2005}
          break;
        case "future":
          this.creatingStepDisplayed = this.$refs.creating3SelectFuture;
          this.transitionStep(this.$refs.creating3SelectFuture);
          this.selectedDate = {'month':5,'day':13,'year':2007}
          break;
      }
    },
    handleDate(direction){
      this.newPostcardToData.name = 'jackie@jackies.world';
       console.log("SELECTED DATE");
      console.log(this.selectedDate);
      let compositeDate = `${this.selectedDate['month']}/${this.selectedDate['day']}/${this.selectedDate['year']}`;
      this.newPostcardToData.date = compositeDate;
      console.log("NEW DATE");
      console.log(this.newPostcardToData.date);
      this.creatingStepDisplayed = this.$refs.postcardCreation;
      this.transitionStep(this.$refs.postcardCreation);
      switch(direction){
        case 'past':
          this.createStepBeforePostcard = this.$refs.creating3SelectPast;
          break;
        case 'future':
          this.createStepBeforePostcard = this.$refs.creating3SelectFuture;
          break;
      }
    },
    transitionStep(stepToDisplay){
      $(".creating-step").fadeOut(1000);
      $(stepToDisplay).fadeIn(1000);

    },
    handleSelectedPostcard(selectedPostcardData){
      let selectedPostcard = selectedPostcardData.postcardEl;
      this.resetPostcards = false;
      this.selectedPostcard = selectedPostcard;
      this.selectedThemeSrc = selectedPostcardData.src;
      $(".newPostcard").not(selectedPostcard).addClass('hidden');
    },
    handleMessageTyping(receivedMessageContent){
      // console.log(receivedMessageContent);
      this.messageContentToSend = receivedMessageContent;

    },
    sendPostcard(){
      //send the current postcard
      let message = $(this.selectedPostcard).find("textarea")[0].value;
      let emailAddress = this.newPostcardToData.name;
      let templates = {'./postcards/greetings.png':'greetingsPostcard',
      './postcards/red-envelope.png':'cnyPostcard',
      './postcards/flavor-packet.png':'chickenPostcard'};
      let chosenTemplate = templates[this.selectedThemeSrc];
       let _this = this;
      if (emailAddress =='jackie@jackies.world'){
        database.addData_Postcard(_this.newPostcardToData,_this.newPostcardFromData,"",_this.selectedThemeSrc,message,false);
            _this.allPostcards = database.getPostcards();
        this.sendSuccess = true;
      } else {
        var data = {
            service_id: 'gmail',
            template_id: chosenTemplate,
            user_id: 'user_KfJuBEVfZzc88RF2AzJZs',
            template_params: {
                'message': message,
                'to_email': emailAddress
            }
        };
       
        $.ajax('https://api.emailjs.com/api/v1.0/email/send', {
            type: 'POST',
            data: JSON.stringify(data),
            contentType: 'application/json'
        }).done(function() {
            // alert('Your mail is sent!');
            database.addData_Postcard(_this.newPostcardToData,_this.newPostcardFromData,"",_this.selectedThemeSrc,message,false);
            _this.allPostcards = database.getPostcards();
            _this.sendSuccess = true;
        }).fail(function(error) {
            _this.sendFailure = true;
            console.log(JSON.stringify(error));
            // alert('Oops... ' + JSON.stringify(error));
        });
      }
      
    },
    backFromSendSuccess(){
      this.sendSuccess = false;
      this.creating = false;
      this.creatingStepDisplayed = this.$refs.creating1;
    },
    backFromSendFailure(){
      this.sendFailure = false;
      this.goBack();
      this.goBack();
    },
    dragPostcard(e){
      e.stopPropagation();
      e.preventDefault();
      if (!this.readingPostcard.currentlyReading && this.animationFinished){
        var element = e.target;
        element = $(element).closest('.postcard-element')[0];
        var offset = $(element).offset();
        var relativeX = (e.pageX - offset.left);
        var relativeY = (e.pageY - offset.top);
        var left = e.pageX - relativeX;
        var top = e.pageY - relativeY;
        $(element).css('left',left);
        $(element).css('top',top);
        this.draggingPostcard = {
          isDragging:true,
          element:element,
          grabbedCoords: {x:relativeX, y:relativeY}
        }
      }
     
    },
    continueDragging(e){
      if (this.draggingPostcard.isDragging == true){
        var elementToDrag = this.draggingPostcard.element;
        var grabbedCoords = this.draggingPostcard.grabbedCoords;
        var top = e.clientY - grabbedCoords.y;
        var left = e.clientX - grabbedCoords.x;
        $(elementToDrag).css('left',left);
        $(elementToDrag).css('top',top);  
        if (left > window.innerWidth/2){
          $(elementToDrag).addClass('leaveToRight');
        } else {
           $(elementToDrag).removeClass('leaveToRight');
        }
      }
    },
    stopDragging(){
      this.draggingPostcard = {
        isDragging:false,
        element:null,
        grabbedCoords:{x:0,y:0}
      }
    },
    readPostcard(thisPostcardElement){
      if( this.readingPostcard.el == null){
        this.animationFinished = false;
        let _this = this;
        thisPostcardElement.classList.add("unclickable");
        $('.postcard-element').not(thisPostcardElement).addClass('goAwayWhileReading unclickable').animate({opacity:0},1200);
        var left = (window.innerWidth-500)/2;
        var top = (window.innerHeight-340)/2-40;
        $(thisPostcardElement).css('top',top);
        $(thisPostcardElement).css('left',left);
        $(thisPostcardElement).css('transition','transform 1s, top 1s, left 1s');
        this.readingPostcard.currentlyReading = true;
        this.readingPostcard.el = thisPostcardElement;
        this.resetReadPostcards = false;
        window.setTimeout(function(){
          _this.animationFinished = true;
           thisPostcardElement.classList.add("selected");
            $(thisPostcardElement).find('button').css("cursor","pointer")
            $(thisPostcardElement).find('button').css("pointer-events","all")
        },1000);     
      }

    },
    stopReading(){
      this.animationFinished = false;
      var thisPostcardElement = this.readingPostcard.el;
      $(thisPostcardElement).removeClass('selected');
      $('.postcard-element').removeClass('goAwayWhileReading');
      var _this = this;
      $('.postcard-element').each(function(){
        let randomPlacement = _this.randomPostcardPlacement();
        $(this).css('left', randomPlacement.leftCSS);
        $(this).css('top',randomPlacement.topCSS);
        if (randomPlacement.leaveToRight){
          $(this).addClass('leaveToRight');
        } else {
           $(this).removeClass('leaveToRight');
        }
      });
      $('.postcard-element').not(thisPostcardElement).animate({opacity:1},1200);
      window.setTimeout(function(){
        $(thisPostcardElement).css('transition','transform 3s');
        _this.readingPostcard.currentlyReading = false;
        _this.readingPostcard.el = null;
        $('.postcard-element').removeClass("unclickable");
        _this.animationFinished = true;
      },1000);
      this.resetReadPostcards = true;
  

    },
    setPostcardToRead(postcardData){
      let updatedData = Object.assign({}, postcardData);
      updatedData.unread = false;
      this.postcardsThatHaveBeenRead.push(updatedData);

    }

  },
  props: {
    name: String,

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.postcard-space{
  height:100%;
  width:100%;
  top:0px;
  margin:auto;
  position:absolute;
  z-index:-1;
  opacity:0;


}





.postcard-element{
  width:500px;
  height:340px;
  z-index:1;
  display:inline-block;
  /*opacity:1;*/
  position:absolute;
  transform: translate(0px);
  transition-delay:4s;
  transition: transform 3s;
  left:0px;
  top:0px;
  pointer-events:all;
  cursor:pointer;
  

  /*margin-right:0px;*/
}
.postcard-element.unreadPostcard{
  /*.postcard-front, .postcard-back{*/
    /*border:4px solid yellow;*/
 
    z-index:5;
  /*}*/
}

.unclickable{
  pointer-events:none;
  cursor:auto;

}

.postcard-element.selected{
  transform: scale(1.4);
  margin:auto;
}

.postcard-element:not(.inView){
  transform: translate(-1200px);
  pointer-events:none;
}

.postcard-element:not(.inView).leaveToRight{
  transform: translate(1200px);
  pointer-events:none;
}
  .postcard-element.goAwayWhileReading{
  transform: translate(-1200px);
  pointer-events:none;

}
.postcard-element.goAwayWhileReading.leaveToRight{
  transform: translate(1200px);
  pointer-events:none;

}

.slower{
  transition:2s transform;
}
/*.newPostcard{
  visibility:visible;
  transition:1s visibility;
}*/
.backButton{
  position:fixed;
  left:32px;
  bottom:18px;
}
.creating-selection{
  width:160px;
  height:160px;
  margin:20px;
  padding:20px;
  display:inline-block;
  border:2px solid;
}

button.createNewPostcard{
  position:fixed;
  right:10px;
  top:30px;
}

.sendPostcard{
  position:fixed;
  right:10px;
  bottom:30px;
  padding:16px 24px;
  font-size:20px;
  border-radius:30px;
}




.messages-nav{
  background:white;
  z-index:2;
  position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;

  h3{
    display:inline-block;
    margin:16px;
  }
  h3.active{
    text-decoration: underline;
  }

}
#sendPostcardResponse{
  z-index:5;
  margin: 0;
  position: absolute;
  top:0px;
  left:0px;
  background-color:rgba(0,0,0,.7);
  color:white;
  width: 100%;
  height: 100%;
  .content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height:280px;
    line-height:40px;
    max-width:800px;
  }
}
button{
  z-index:3;
}

</style>
