<template>
  <div  class="thing" >

    <div v-on:contextmenu="customMenu" v-on:click="clickHighlight" v-bind:class="{isHighlighted:isHighlighted,isSecret:(isSecret&&!authenticationSucceeded)}" v-on:dblclick="viewThing" class="preview">
        <img class="previewImg" v-bind:src=imgSrc />
        <h4 class="title">{{data.name}}</h4>
    </div>


    <div v-if="thingInFocus" class="thingInFocus">
        <a v-on:click="closeThing" v-if=thingInFocus >Close</a><br>
        <img class="previewImg" v-bind:src=imgSrc />
        <!-- <canvas id="canvas" width=340 height=400></canvas> -->
        <h4 class="title">{{data.name}}</h4>
        <button v-on:click="openInSketchbook">Open in Sketchbook</button>
        <div class="tags">
          <span v-on:click="goToSubspace(tag)" v-for="tag in data.tags" v-bind:key=tag class="tagLink">{{tag}}</span>
        </div>
    </div>
    <SecretAuthentication v-if="attemptedAccess"  v-on:authenticationSuccess="openSecret" v-on:authenticationFail="denySecret" />
    <CustomMenu v-if="showMenu" v-on:clickActionMessage="receiveClickActionFromMenu" v-bind:actions="menuActions"/>
    <!-- <canvas id="canvas" width="340px" height="400px"></canvas> -->
  </div>
</template>

<script>
import SecretAuthentication from './SecretAuthentication.vue';
import CustomMenu from './CustomMenu.vue';
import { fabric } from 'fabric';
// import * as tagStructure from './tagStructure.js';

export default {
  name: 'Thing',
  components: {
    SecretAuthentication,
    CustomMenu
  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"
    menu: function(newVal){
        this.showMenu = (newVal == true);
    }
  },
  data(){
    let isSecret = this.$props.data.tags.includes("secrets");

    let menuActions = [
    "Edit","Delete"];
    let canvasData = this.data.canvasData;
    let imgSrc="";
    if (canvasData != "" &&  this.data.src == ""){
      let canvas =  new fabric.Canvas("canvas",{height:400,width:340});
      // console.log(canvas);
      canvas.loadFromJSON(canvasData, () =>{
         let svg = canvas.toSVG();
          var svgBlob = new Blob([svg], {type: 'image/svg+xml'});
          imgSrc = URL.createObjectURL(svgBlob);
          this.updateImgURL(imgSrc);
      });
     
    } else {
      console.log('img src');
      imgSrc=this.data.src;
    }
 
    // console.log(imgSrc);
    return{
      thingInFocus:false,
      isHighlighted:false,
      isSecret: isSecret,
      attemptedAccess: false,
      authenticationSucceeded: false,
      menuActions: menuActions,
      showMenu: false,
      imgSrc: imgSrc
    }
  },
  computed:{
  },
  mounted(){
    console.log(window);

    // window.addEventListener("click",function(e){
    //   console.log("clicking page from thing");
    //   console.log(e.target);
    //   if (e.target.className != "customMenu"){
    //     console.log("we are not clicking in a customMenu")
    //     this.showMenu=false;
    //   }
    // });
  },
  methods:{
    updateImgURL: function(result){
      this.imgSrc = result;
    },
    customMenu: function(){
      console.log("Tell space to remove other menus from screen")
      this.$emit("clearCustomMenusMessage",this.data);

      // console.log("here is a custom menu");
      // //Update data
      // this.showMenu = true;
      // //Update prop
      // // this.menu = true;
    },
    receiveClickActionFromMenu: function(action){
      if (action == "Edit"){
        //Edit this by sending it to the sketchbook
        console.log("sending "+this.name+"to sketchbook")
      } else if (action == "Delete"){
        //Delete this by sending it to the garbage
        console.log("placeholder for deleting: "+this.name)
      }
    },
    clickHighlight: function(){
      console.log("click highlighted")
      this.isHighlighted = true;
    },
   viewThing: function(){
    console.log("VIEW THING");
    if (this.isSecret){
      if (this.authenticationSucceeded){
        console.log("AUTHENTICATION SUCCEEDED AND VIEWING THING");
        this.thingInFocus = true;
      } else {
        this.attemptedAccess = true;
      }
    } else {
      if (!this.thingInFocus){
        this.thingInFocus = true;
      }
    }
    },
    closeThing: function(){
      console.log("CLOSE THING");
      this.thingInFocus = false;
    },
    openSecret(){
      this.authenticationSucceeded = true;
      this.viewThing();
      this.attemptedAccess = false;
    },
    denySecret(){
      this.attemptedAccess = false;
    },
    goToSubspace(tag){
      console.log("go to the subspace for:"+tag);
      let msg = {
        destinationSpace : tag
      }
      this.thingInFocus = false;
      this.$emit('goToSubspaceMessage',msg);
    },
    openInSketchbook(){
      console.log("openInSketchbook");
      let data = {
        destination: "sketchbook",
        thingId: this.data.id
      }
      this.$root.$emit('switchApplicationMessage',data);
    }
  },
  props: {
    name: String,
    data: Object,
    menu: Boolean
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
