.thing{
  position:relative;
  .previewImg{
    min-width:100px;
    min-height:100px;
    max-width:150px;
    max-height:200px;

  }
 
}

.isHighlighted{
  border:1px dotted black;
}
.isSecret{
  img{
    filter:blur(30px);
  }
  .title{
    filter:blur(5px);
  }
}

.thingInFocus{
  z-index:1;
  background-color:pink;
  position:fixed;
  border-radius:40px;
  top:5%;
  left:5%;
  width:80%;
  height:80%;
  padding:5%;
  .previewImg{
    max-width:400px;
    max-height:500px;
  }

  .tags{
    margin-top:30px;
    span{
      padding:5px 10px ;
      margin:15px;
      background-color:white;
    }
  }
}

</style>
