<p></p><template>
  <div class="navigation">
      <a class="goToBedroomLink" v-on:click="goToBedroom">&larr;  Go Back to bedroom</a>
      <div class="applicationTitle">
        <h2>{{currentAppName}}</h2>
        <img v-show="appId != 'panda-guide'" src="/UI/pandaicon.svg" v-on:click="openGuide()">
      </div>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
  props: {
    msg: String,
    currentAppName:String,
    appId:String
  },
  methods:{
    goToBedroom:function(){
      console.log("goToBedroomMessage sending");
      this.$root.$emit('goToBedroomMessage')

    },
    openGuide:function(){
      this.$root.$emit('openGuideFromAppMessage',this.appId)
    }
  }
}
</script>

