<template>
  <div class="explorer" >
    <Navigation currentAppName="Cloud Explorer" appId="explorer"/>

    <div id="explorer-nav">
      <input v-show=!currentlyEditing type="text" id="searchbar" v-bind:value=searchbarURL>
      <span v-show=currentlyEditing>Previewing changes to jackiesworld.com</span>

      <div v-show="!currentlyEditing" id="home-and-faves">
        <span class="group">
          <a v-on:click="goHome">home</a>
          <a v-if=homepage v-on:click="editSite">Edit site</a>
        </span>
        <span v-on:click="openFaves" class="group"><a>faves</a></span>
      </div>
    </div>
    <iframe v-bind:class="{'duringEditing':currentlyEditing}" 
    id="iframe" 
  v-bind:src=siteURL
  srcdoc=""
     ></iframe>

    <div class="sidebar" v-bind:class="{'visible':showSidebar}">
      <div v-show=currentlyEditing class="editingSite">
        <button v-on:click="cancelEditing">x</button>
        <h2>editing jackiesworld.com</h2>
        <!-- <textarea ref="codingArea"></textarea> -->
        <div class="codingArea" ref="codingArea"></div>
        <button v-on:click="saveChanges">Save and Finish</button><br>
        <button v-on:click="cancelEditing">Cancel</button>
      </div>
      <div v-show=(!currentlyEditing) class="faves">
        <a v-on:click="closeFaves">Close</a>
        <h2>my faves</h2>
        <ul>
          <li v-on:click="clickFaveLink"
          displaysrc="http://search.deviantart.com/?section=browse&qh=boost%3Apopular+age_sigma%3A24h+age_scale%3A5&q=Naruto"
          src="https://web.archive.org/web/20070611183513if_/http://search.deviantart.com/?section=browse&qh=boost%3Apopular+age_sigma%3A24h+age_scale%3A5&q=Naruto">naruto artwork</li>
          <li v-on:click="clickFaveLink"
          displaysrc="http://www.musogato.com/avatar/"
          src="https://web.archive.org/web/20060702223502if_/http://www.musogato.com/avatar/">cool avatar site</li>
          <li v-on:click="clickFaveLink" 
          displaysrc="http://en.wikipedia.org/wiki/Easy-Bake_Oven"
          src="https://web.archive.org/web/20071112014413if_/http://en.wikipedia.org/wiki/Easy-Bake_Oven">easy bake oven</li>
          <li v-on:click="clickFaveLink"
          displaysrc="http://yahooligans.yahoo.com/"
          src="https://web.archive.org/web/20060610124851if_/http://yahooligans.yahoo.com/">yahooligans</li>
          <li v-on:click="clickFaveLink"
          displaysrc="http://www.oekakiart.com/dhpractice/"
          src="https://web.archive.org/web/20070526024402if_/http://www.oekakiart.com/dhpractice/">avatar oekaki</li> 
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Navigation from './Navigation.vue';
import CodeMirror from 'codemirror';
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/panda-syntax.css';
import 'codemirror/mode/htmlmixed/htmlmixed.js';
import  $ from 'jquery';
// import 'codemirror/lib/codemirror.js';
// import SecretAuthentication from './SecretAuthentication.vue';
// import CustomMenu from './CustomMenu.vue';
// import { fabric } from 'fabric';
// // import * as tagStructure from './tagStructure.js';

export default {
  name: 'Explorer',
  components: {
    Navigation
  },
  watch:{
    //We are a menu on the screen. when we get "menu is false"
    menu: function(newVal){
        this.showMenu = (newVal == true);
    }

  },
  data(){

     let siteHTML = `<!DOCTYPE html>
<html>
<head>
<title>Jackie's World</title>
<style>
  img{
    max-width:100%;
  }
</style>
</head>
<body>

<h1>Jackie's World</h1>
<p>Hi, welcome to Jackie's world! This is my very own website!</p>
<h2> My favorite foods</h2>
<p> I love to eat barbecue ribs and chili. The more spicy the better!</p>
<h2> My art</h2>
<p>[tbd]</p>
<h2> My favorites</h2>
<p>I LOOOOOOOOOOOVE Avatar: The Last Airbender!! I like Kiki's Delivery Service too. ^___^</p>

</body>
</html>`;
    console.log(localStorage.getItem('siteHTML'));
    if (localStorage.getItem('siteHTML') == null){
       localStorage.setItem("siteHTML",siteHTML);
    }
    // let siteURL = "https://en.wikipedia.org"

    return {
      siteURL: "https://en.wikipedia.org",
      searchbarURL: "jackiesworld.com",
      homepage:true,
      showSidebar: false,
      currentlyEditing: false,
      siteHTML: siteHTML,
      codeMirror: null


      }
    
  },
  computed:{

   
  },
  mounted(){
    console.log(CodeMirror);
    document.getElementById("iframe").setAttribute("srcdoc",this.siteHTML);
    let codingArea = this.$refs.codingArea;
    this.codeMirror = CodeMirror(codingArea,{
      value: this.siteHTML,
      mode: "htmlmixed",
      theme: 'panda-syntax'
    });

    $('.goToBedroomLink').click(()=>{

        // this.savePageState(this.currentL.fabricCanvas,this.currentLThing);
        // this.savePageState(this.currentR.fabricCanvas,this.currentRThing);
    })

    document.getElementById('searchbar').addEventListener('keydown',function(e){
      // e.preventDefault();
      if (e.keyCode==13){
        //submit the text, attempt to go to that thing
        console.log(e.target.value);
        var attemptedURL = e.target.value;
        this.searchbarURL = attemptedURL;

        //Route to an "oops something went wrong"


        // var waybackURL = "https://archive.org/wayback/available?url="+attemptedURL+"&timestamp=20060500";
        //  waybackURL = "https://archive.org";
        //  waybackURL = "http://wayback.archive-it.org/all/timemap/cdx?url=archive-it.org";

         // waybackURL = "http://web.archive.org/cdx/search/cdx?url=archive.org"


// $.getJSON(waybackURL,function(json){
//   console.log(json);
// });
  
      }
    })
  },
  methods:{
    editSite: function(){
      this.showSidebar = true;
      this.currentlyEditing = true;
      var _this = this;
      setTimeout(function(){
        _this.codeMirror.refresh()
      });


      this.codeMirror.on("change",function(cm){
        console.log("code mirror changed");
        var newValue = cm.getValue()
        _this.siteHTML = newValue;
        document.getElementById("iframe").setAttribute("srcdoc",_this.siteHTML);
      })
      // let codingArea = this.$refs.codingArea;
      // let input = this.siteHTML;
      //     input = input.replace( /\n/g, "<br>");
      // codingArea.innerText = input;
      // let codingArea = document.getElementById('codingArea');
      // console.log(codingArea);
      // // console.log(document.getElementById("codingArea"));
      // // codingArea.innerText = this.siteHTML;
      // // this.$refs.codingArea.innerText = this.siteHTML;

    },
    saveChanges: function(){
      let newHTML = this.codeMirror.getValue();
      localStorage.setItem("siteHTML",newHTML);
      this.showSidebar = false;
      this.currentlyEditing = false;

    },
    cancelEditing: function(){
      let previousHTML = localStorage.getItem("siteHTML");
      document.getElementById("iframe").setAttribute("srcdoc",previousHTML);
      this.showSidebar = false;
      this.currentlyEditing = false;
    },
    goHome: function(){
       document.getElementById("iframe").setAttribute("srcdoc",this.siteHTML);
    },
    openFaves: function(){
      this.showSidebar = true;
      this.currentlyEditing = false;
       // document.getElementById("iframe").removeAttribute("srcdoc");
    },
    closeFaves:function(){
      this.showSidebar = false;
    },
    clickFaveLink:function(e){
      // let src = e.target.src;
      console.log(e.target.getAttribute('src'));
      this.siteURL = e.target.getAttribute('src');
      document.getElementById("iframe").removeAttribute("srcdoc");
      this.homepage = false;
    }
  },
  props: {
    name: String,
    data: Object,
    menu: Boolean
  }
}
</script>

